@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

.note_container {
    padding: 1rem;
    /* border: 2px solid red; */
    margin: 1rem 0;
    border-radius: 3rem;

    background-color: rgba(0, 0, 0, 0.451);
    background-image: linear-gradient(to right top, #260000c2, rgba(9, 255, 222, 0.303));
}

#top {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    word-wrap: break-word;

}

#top h2 {
    font-size: 1.5rem;
    text-align: center;
    /* for text gredient color. */
    background-image: linear-gradient(45deg, #56e9f7, #feffff);
    background-size: 100%;
    background-repeat: repeat;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-background-clip: text;
    -moz-text-fill-color: transparent;
    font-family: 'Ubuntu', sans-serif;
}

#top p,
#setting button {
    /* border: 2px solid red; */
    border-radius: 3rem;
    padding: 0.5rem 2rem;
    font-size: 1rem;
    background-color: rgba(0, 0, 0, 0.451);
    background-image: linear-gradient(to right top, rgba(9, 255, 222, 0.303), #260000c2);
}

#down {
    padding: 1rem;
}

#down,
#setting button {
    cursor: pointer;
    font-size: 1rem;
    word-wrap: break-word;
    /* for text gredient color. */
    background-image: linear-gradient(45deg, #f0f972, #b2e6f4);
    background-size: 100%;
    background-repeat: repeat;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-background-clip: text;
    -moz-text-fill-color: transparent;
    font-family: 'Ubuntu', sans-serif;

    /* for removing blue shadow while tapping on button on touch screen */
    -webkit-tap-highlight-color: transparent;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

#setting {
    margin-top: 2.5rem;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;

}

/* modal part css */
#modal {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 1rem;
    /* for color gradient  */
    background-color: rgba(0, 0, 0, 0.451);
    background-image: linear-gradient(to right top, #260000c2, rgba(9, 255, 222, 0.303));
    font-family: 'Lobster', cursive;
}

#modal h2 {
    font-size: 2rem;
    margin: 1rem;
    /* for text gredient color. */
    background-image: linear-gradient(45deg, #1ff9f9, #c82351);
    background-size: 100%;
    background-repeat: repeat;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-background-clip: text;
    -moz-text-fill-color: transparent;
}

#modal form {
    height: 90%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

#modal form input {
    width: 70%;
    margin: 1rem;
    height: 5%;
    font-size: 1.3rem;
    border-radius: 5rem;
    /* for color gradient  */
    background-color: rgba(0, 0, 0, 0.451);
    background-image: linear-gradient(to right top, #260000c2, rgba(9, 255, 222, 0.303));
    text-align: center;
    font-family: 'Ubuntu', sans-serif;

    /* for text gredient color. */
    background-image: linear-gradient(45deg, #f0f972, #b2e6f4);
    background-size: 100%;
    background-repeat: repeat;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-background-clip: text;
    -moz-text-fill-color: transparent;
}

#modal form textarea {
    width: 70%;
    margin: 1rem;
    height: 40%;
    border-radius: 1.5rem;
    padding: 0.5rem;
    font-size: 1.3rem;
    /* for color gradient  */
    background-color: rgba(0, 0, 0, 0.451);
    background-image: linear-gradient(to right top, #260000c2, rgba(9, 255, 222, 0.303));
    /* text-align: center; */
    font-family: 'Ubuntu', sans-serif;


    /* for text gredient color. */
    background-image: linear-gradient(45deg, #f0f972, #b2e6f4);
    background-size: 100%;
    background-repeat: repeat;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-background-clip: text;
    -moz-text-fill-color: transparent;
}